import React from 'react';
import { connectSearchBox } from 'react-instantsearch-dom';
import classNames from 'classnames';
import styled from '@emotion/styled';
import { css } from 'emotion';
import SearchSvg from '../images/SearchSvg';
import styles from './Input.module.css';

const focus = props => css`
  background: white;
  color: ${props.theme.darkBlue};
  cursor: text;
  width: 5em;
  + ${styles.searchIcon} {
    color: ${props.theme.darkBlue};
    margin: 0.3em;
  }
`;

const collapse = props => css`
  width: 0;
  cursor: pointer;
  color: ${props.theme.lightBlue};
  + ${styles.searchIcon} {
    color: white;
  }
  ${props.focus && focus(props)}
  margin-left: ${props.focus ? `-1.6em` : `-1em`};
  padding-left: ${props.focus ? `1.6em` : `1em`};
  ::placeholder {
    color: ${props.theme.gray};
  }
`;

const expand = props => css`
  background: ${props.theme.veryLightGray};
  width: 6em;
  margin-left: -1.6em;
  padding-left: 1.6em;
  + ${styles.searchIcon} {
    margin: 0.3em;
  }
`;

const Input = styled.input`
  outline: none;
  border: none;
  font-size: 1em;
  background: white;
  transition: ${props => props.theme.shortTrans};
  border-radius: ${props => props.theme.smallBorderRadius};
  {hightlight-next-line}
  ${props => (props.collapse ? collapse(props) : expand(props))};
`;

export default connectSearchBox(({ refine, ...rest }) => {
  const searchCustom = '';
  const preventSubmit = e => {
    e.preventDefault();
  };

  return (
    <form className={classNames('formElement', styles.form)} onSubmit={preventSubmit}>
      <SearchSvg className={styles.searchIcon} />
      <Input
        className={'searchInput ' + searchCustom}
        type="text"
        placeholder="Search"
        aria-label="Search"
        onChange={e => refine(e.target.value)}
        {...rest}
      />
    </form>
  );
});
