import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import styled from '@emotion/styled';
import config from '../../config';
import styles from './RightSidebar.module.css';

// eslint-disable-next-line no-unused-vars
const ListItem = styled(({ className, active, level, ...props }) => {
  return (
    <li className={className}>
      <a href={props.to} {...props} />
    </li>
  );
})`
  list-style: none;

  a {
    color: #5c6975;
    text-decoration: none;
    font-weight: ${({ level }) => (level === 0 ? 700 : 400)};
    padding: 0.45rem 0 0.45rem ${props => 2 + (props.level || 0) * 1}rem;
    display: block;
    position: relative;

    &:hover {
      background-color: #ebebeb;
      color: rgb(36, 41, 46);
    }

    ${props =>
      props.active &&
      `
color: #663399;
border-color: rgb(230,236,241) !important;
border-style: solid none solid solid;
border-width: 1px 0px 1px 1px;
background-color: #fff;
`} // external link icon
    svg {
      float: right;
      margin-right: 1rem;
    }
  }
`;

const SidebarLayout = props => (
  <StaticQuery
    query={graphql`
      query {
        allMdx {
          edges {
            node {
              fields {
                slug
              }
              tableOfContents
            }
          }
        }
      }
    `}
    render={({ allMdx }) => <Contents allMdx={allMdx} {...props} />}
  />
);

const Contents = ({ allMdx, location }) => {
  let contentsFragment, finalNavItems;

  if (allMdx.edges !== undefined && allMdx.edges.length > 0) {
    allMdx.edges.forEach(item => {
      let innerItems;
      if (item !== undefined) {
        if (
          item.node.fields.slug === location.pathname ||
          config.gatsby.pathPrefix + item.node.fields.slug === location.pathname
        ) {
          if (item.node.tableOfContents.items) {
            innerItems = item.node.tableOfContents.items.map((innerItem, index) => (
              <ListItem key={index} to={innerItem.url} level={1}>
                {innerItem.title}
              </ListItem>
            ));
          }
        }
      }
      if (innerItems) {
        finalNavItems = innerItems;
      }
    });
  }

  if (finalNavItems && finalNavItems.length) {
    contentsFragment = (
      <ul className={'rightSideBarUL'}>
        <div className={'rightSideTitle'}>CONTENTS</div>
        {finalNavItems}
      </ul>
    );
  } else {
    contentsFragment = <ul></ul>;
  }

  return <aside className={styles.sidebar}>{contentsFragment}</aside>;
};

export default SidebarLayout;
