import React from 'react';
import classNames from 'classnames';
import { StaticQuery, graphql } from 'gatsby';
import GitHubButton from 'react-github-btn';
import Link from './Link';
import ImageLogo from './images/ImageLogo';
import config from '../../config.js';
import AlgoliaSearch from './AlgoliaSearch';
import DocSearch from './DocSearch';
import styles from './Header.module.css';
import LeftSidebar from './LeftSidebar';

const help = require('./images/help.svg');
const textLogo = require('./images/logo-text.png');
const isAlgoliaSearchEnabled = !!(
  config.header.search &&
  config.header.search.enabled &&
  config.header.search.algoliaAppId
);
const isDocSearchEnabled = !!(
  config.header.search &&
  config.header.search.enabled &&
  config.header.search.algoliaApiKey &&
  config.header.search.indexName
);

let searchIndices = [];
if (isAlgoliaSearchEnabled && config.header.search.indexName) {
  searchIndices.push({
    name: `${config.header.search.indexName}`,
    title: `Results`,
    hitComp: `PageHit`,
  });
}

const Header = () => (
  <StaticQuery
    query={graphql`
      query headerTitleQuery {
        site {
          siteMetadata {
            headerTitle
            githubUrl
            helpUrl
            logo {
              link
              image
            }
            headerLinks {
              link
              text
            }
          }
        }
      }
    `}
    render={({ site }) => <Contents site={site} />}
  />
);

const Contents = ({ site }) => {
  const {
    siteMetadata: { headerTitle, githubUrl, helpUrl, logo, headerLinks },
  } = site;
  const finalLogoLink = logo.link !== '' ? logo.link : '/';
  let githubButtonFragment, helpIconFragment, logoImageFragment, searchFragment, titleFragment;

  if (logo.image !== '') {
    logoImageFragment = (
      <img className={'img-responsive imageLogo'} src={logo.image} alt={'Image Logo'} />
    );
  } else {
    logoImageFragment = <ImageLogo />;
  }

  if (headerTitle) {
    titleFragment = (
      <div className={'headerTitle'} dangerouslySetInnerHTML={{ __html: headerTitle }} />
    );
  } else {
    titleFragment = (
      <div className={'headerTitle'}>
        <img className={'textLogo'} src={textLogo} alt={'Text Logo'} />
      </div>
    );
  }

  if (isAlgoliaSearchEnabled) {
    searchFragment = (
      <div className={'searchWrapper navBarUL'}>
        <AlgoliaSearch collapse indices={searchIndices} />
      </div>
    );
  } else if (isDocSearchEnabled) {
    searchFragment = (
      <div className={'searchWrapper navBarUL'}>
        <DocSearch collapse />
      </div>
    );
  }

  const headerLinksFragment = headerLinks.map((link, key) => {
    if (link.link !== '' && link.text !== '') {
      return (
        <li key={key}>
          <a
            href={link.link}
            target="_blank"
            rel="noopener noreferrer"
            dangerouslySetInnerHTML={{ __html: link.text }}
          />
        </li>
      );
    }
  });

  if (helpUrl !== '') {
    helpIconFragment = (
      <li>
        <a href={helpUrl}>
          <img src={help} alt={'Help icon'} />
        </a>
      </li>
    );
  }

  if (githubUrl !== '') {
    githubButtonFragment = (
      <>
        <li className={styles.divider}></li>
        <li className={styles.githubBtn}>
          <GitHubButton href={githubUrl} data-show-count="true" aria-label="Star on GitHub">
            Star
          </GitHubButton>
        </li>
      </>
    );
  }

  return (
    <div className={'navBarWrapper'}>
      <nav className={'navbar navbar-expand-lg navbar-default navBarDefault'}>
        <div className={'navbar-header navBarHeader'}>
          <Link to={finalLogoLink} className={'navbar-brand navBarBrand'} tabIndex="-1">
            {logoImageFragment}
            {titleFragment}
          </Link>
          {searchFragment}
          <button
            type="button"
            className={'navbar-toggle collapsed navBarToggle'}
            data-toggle="collapse"
            data-target="#navbar"
            aria-expanded="false"
            aria-controls="navbar"
          >
            <span className={'sr-only'}>Toggle navigation</span>
            <span className={'icon-bar'}></span>
            <span className={'icon-bar'}></span>
            <span className={'icon-bar'}></span>
          </button>
        </div>
        <div id="navbar" className={'navbar-collapse collapse navBarCollapse'}>
          <div className={styles.sidebarContainer}>
            <LeftSidebar />
            <hr />
          </div>
          <ul
            className={classNames(
              'nav',
              'navbar-nav',
              'navBarUL',
              'navBarNav',
              'navbar-right',
              styles.navBarULRight
            )}
          >
            {headerLinksFragment}
            {helpIconFragment}
            {githubButtonFragment}
          </ul>
        </div>
      </nav>
    </div>
  );
};

export default Header;
