import React from 'react';
import classNames from 'classnames';
import AnchorTag from './AnchorTag';
import Code from './Code';
import CodeBlock from './CodeBlock';
import Pre from './Pre';
import styles from './styles.module.css';

function getId(props) {
  const { children } = props;
  if (Array.isArray(children)) {
    return children[0].href;
  } else {
    return children.replace(/\s+/g, '').toLowerCase();
  }
}

/* eslint-disable react/display-name */
export default {
  h1: ({ className, ...props }) => (
    <h1 id={getId(props)} {...props} className={classNames(className, styles.h1)} />
  ),
  h2: ({ className, ...props }) => (
    <h2 id={getId(props)} {...props} className={classNames(className, styles.h2)} />
  ),
  h3: ({ className, ...props }) => (
    <h3 id={getId(props)} {...props} className={classNames(className, styles.h3)} />
  ),
  h4: ({ className, ...props }) => (
    <h4 id={getId(props)} {...props} className={classNames(className, styles.h4)} />
  ),
  h5: ({ className, ...props }) => (
    <h5 id={getId(props)} {...props} className={classNames(className, styles.h5)} />
  ),
  h6: ({ className, ...props }) => (
    <h6 id={getId(props)} {...props} className={classNames(className, styles.h6)} />
  ),
  p: ({ className, ...props }) => <p {...props} className={classNames(className, styles.p)} />,
  pre: Pre,
  code: CodeBlock,
  inlineCode: props => <Code {...props} />,
  a: props => <AnchorTag {...props} />,
  // TODO add `img`
  // TODO add `blockquote`
  // TODO add `ul`
  // TODO add `li`
  // TODO add `table`
};
